@import "../sizings.scss";
@import "../colors.scss";

.article-wrapper {
  padding: $large-size;
  & + .article-wrapper { border-top: $border-size solid $dark-gray; }

  article {
    display: flex;
    flex-direction: column;
    gap: $large-size;
    margin: 0 auto;
    max-width: $max-content-width;
  }
}
